import { CustomElement, Prop, Watch } from 'custom-elements-ts';

export interface ARToastMessage {
  severity?: string;
  summary?: string;
  detail?: string;
}

@CustomElement({
  tag: 'ar-toast',
  template: `<div class=\"ar-toast\"></div> <template id=\"toast-template\"> <div class=\"ar-toast-item-container ar-state-highlight ar-corner-all ar-shadow\"> <div class=\"ar-toast-item\"> <div class=\"ar-toast-icon-close\"></div> <span class=\"ar-toast-image\"><i id=\"icon\" class=\"icon\"></i></span> <div class=\"ar-toast-message\"> <span id=\"summary\" class=\"ar-toast-title\">Summary</span> <p id=\"detail\">Details</p> </div> <div class=\"clear\"></div> </div> </div> </template>`,
  style: ':host{font-family:"Helvetica Neue",Roboto,sans-serif}:host *{box-sizing:border-box}:host .ar-toast{position:fixed;top:20px;right:20px;width:20em;z-index:9999}:host .ar-toast-item-container{position:relative;margin:0 0 10px 0;opacity:0;transition:opacity 1.5s}:host .ar-toast-item-container.ar-state-highlight{border-color:#156090;background:#186ba0;color:#fff}:host .ar-toast-item-container.ar-state-highlight.info{background-color:#2196f3;border-color:#2196f3}:host .ar-toast-item-container.ar-state-highlight.error{background-color:#f44336;border-color:#f44336}:host .ar-toast-item-container.ar-state-highlight.warn{background-color:#ffb300;border-color:#ffb300}:host .ar-toast-item-container.ar-state-highlight.success{background-color:#4caf50;border-color:#4caf50}:host .ar-toast-item-container.ar-corner-all{-moz-border-radius:3px;-webkit-border-radius:3px;border-radius:3px}:host .ar-toast-item-container.ar-shadow{-webkit-box-shadow:0 1px 3px 0 rgba(0,0,0,0.3);-moz-box-shadow:0 1px 3px 0 rgba(0,0,0,0.3);box-shadow:0 1px 3px 0 rgba(0,0,0,0.3)}:host .ar-toast-item-container.show{opacity:1}:host .ar-toast-item{position:relative;display:block;padding:.5em 1em}:host .ar-toast-item p{padding:0;margin:0}:host .ar-toast-icon-close{position:absolute;top:4px;right:4px;cursor:pointer}:host .ar-toast-icon-close:after{content:"Ã—";position:absolute;right:5px}:host .ar-toast-title{font-weight:bold;padding:0 0 .5em 0;display:block}:host .ar-toast-image{position:absolute;display:inline-block;left:.5em;top:.25em;padding:0;font-size:2em;border:2px solid;border-radius:50%;width:25px;height:25px}:host .ar-toast-image .icon{position:absolute;font-style:normal;width:20px;height:20px}:host .ar-toast-image .icon:before,:host .ar-toast-image .icon:after{content:"";position:absolute;background-color:#fff;pointer-events:none}:host .ar-toast-image .icon.icon-success{top:4px;left:8px}:host .ar-toast-image .icon.icon-success:before{width:2px;height:15px;transform:rotate(45deg);border-radius:8px;right:14px}:host .ar-toast-image .icon.icon-success:after{bottom:6px;left:-3px;width:2px;height:9px;transform:rotate(-45deg);border-radius:8px}:host .ar-toast-image .icon.icon-info:before,:host .ar-toast-image .icon.icon-warn:before{width:3px;height:10px;border-radius:4px;bottom:8px;left:9px}:host .ar-toast-image .icon.icon-info:after,:host .ar-toast-image .icon.icon-warn:after{width:3px;height:4px;top:14px;left:9px;border-radius:8px}:host .ar-toast-image .icon.icon-warn{top:0;left:1px;transform:rotate(180deg)}:host .ar-toast-image .icon.icon-error:before{transform:rotate(45deg)}:host .ar-toast-image .icon.icon-error:after{transform:rotate(-45deg)}:host .ar-toast-image .icon.icon-error:before,:host .ar-toast-image .icon.icon-error:after{width:2px;height:16px;border-radius:8px;left:9px;top:2px}:host .ar-toast-message{padding:0 0 .25em 0;margin-left:2.5em}:host .ar-toast-message p{font-weight:normal}:host clear{clear:both}'
})
export class ARToastElement extends HTMLElement {

  @Prop() duration;
  @Prop() message;

  @Watch('message')
  onPropertyChangedMessage(value: any) {
    if (value.new) {
      this.showToast(value.new)
    }
  }

  showToast(message: ARToastMessage) {
    const template: any = this.shadowRoot.querySelector('#toast-template');
    const fragment: DocumentFragment = template.content.cloneNode(true);

    const container = fragment.querySelector('.ar-toast-item-container');
    container.classList.add(message.severity);
    container.querySelector('#icon').classList.add(`icon-${message.severity}`);
    container.querySelector('#summary').innerHTML = message.summary;
    container.querySelector('#detail').innerHTML = message.detail;

    this.shadowRoot.querySelector('.ar-toast').appendChild(fragment);
    
    const target: HTMLElement = this.shadowRoot.querySelector('.ar-toast .ar-toast-item-container:last-child');
    this._startTransition(target);
  }

  private _startTransition(target: HTMLElement) {
    const timeout = setTimeout(() => this._endTransition(target), this.duration);
    const handler: EventListener = () => {
      clearTimeout(timeout);
      this._endTransition(target);
    };
    target.classList.add('show');
    target.querySelector('.ar-toast-icon-close').addEventListener('click', handler);
  }

  private _endTransition(target: HTMLElement) {
    target.classList.remove('show');
    setTimeout(() => target && target.remove(), 1500);
  }

}